<template>
    <div class="feedback-layout">
        <header class="feedback-header">
            <a @click="goPrePage()" class="feedback-cancle">取消</a>
            <h2 class="feedback-title">写评论</h2>
            <button  class="feedback-send" id="feedback-send" :disabled="!isAble" @click="sendMsg()">发送</button>
        </header>
        <div class="feedback-content-box">
            <textarea class="feedback-content" placeholder="写评论......" name="content" id="feedback-area" cols="10" rows="20" v-model="feedContent">
            </textarea>
        </div>
    </div>
</template>
<script>
  import autoSize from 'autosize';
  import Api from '../../api/api';
  import scriptFilter from '../../utils/filterScript'
    export default{
        data(){
            return {
                isAble:false,
                feedContent:'',
                articleId:-1,

            }
        },
        mounted(){
            autoSize(document.querySelectorAll('textarea'));
            this.articleId = this.$route.params.id;
            /**
             * 获得焦点,改变样式
             */
            const contentBox = $('#feedback-area');

            const feedBtn = $('#feedback-send');
            const _this = this;
            /**
             * 获得焦点,改变样式
             */
            contentBox.focus();
            _this.changeBtnBg(_this,contentBox,feedBtn);
        },
        methods:{
            sendMsg(){
               if(scriptFilter(this.feedContent)){
                   this.showMsg("内容不能包含奇怪字符!");
                   return;
               }
              /**
               * 限制发送内容不能为空
               */
              if(this.feedContent!==null && this.feedContent!==''){
                    this.commitComment(this.articleId);
                }else{
                    this.showMsg('请填写评论内容哦');
                }
            },
          /**
           * 对新闻的评论
           * */
          commitComment(articleId){
              const _this = this;
              if(articleId!==-1 && articleId!==''){
                this.$http.post(Api.COMMENTNEWS,{
                  content:_this.feedContent,
                  article_id:articleId,
                  status:1
                }).then((res)=>{
                  //评论成功后回到上一个界面
                  if(res.status ===200){
                    history.back(-1);//返回上一页
//                    _this.$router.replace({path: `/newsdetail/${articleId}`});
                  }else{
                    _this.showMsg("留言失败，请重试!");
                    _this.$store.dispatch('hideLoading');
                  }
                },(err)=>{
                  _this.showMsg("提交失败，请重试!");
                  _this.$store.dispatch('hideLoading');
                });
              }
          },
          showMsg(str){
            this.$toast({
              message: str,
              duration: 2000,
            });
          },
            /**
             * 返回上一级元素
             */
            goPrePage(){
                window.history.go(-1);
            },
            setBackGround(ele,bg,color,border) {
                ele.css({
                    'background':bg,
                    'color':color,
                    'border':border
                })
            },
            /**
             * 根据输入内容设置发送按钮的样式
             * @param _this
             * @param contentBox:内容dom元素
             * @param feedBtn:按钮元素
             */
            changeBtnBg(_this,contentBox,feedBtn){
                $(document).on('input propertychange', 'textarea', function() {
                    //function code
                    _this.feedContent = contentBox.val();
                    if(_this.feedContent!=='null' &&_this.feedContent.length>0){
                        _this.isAble = true;
                        _this.setBackGround(feedBtn,'#20a0ff','#ffffff','1px solid #20a0ff');
                    }else{
                        _this.isAble = false;
                        _this.setBackGround(feedBtn,'#ffffff','#9c9c9c','1px solid #9c9c9c');
                    }
                });
            }
        },
      watch:{
        $route(to){

          //对传递的id进行验证(防止篡改)
          let reg=/leavemsg\/\d+/;
          if(reg.test(to.path)){
            let newsId=this.$route.params.id || 0;
            this.commitComment(newsId);
          }
        }
      },
    }
</script>
<style scoped>
    @import "../../assets/css/feedback.css";
</style>



